var paralax, trap;

paralax = function(target, rate) {
  var move;
  move = window.scrollY * rate;
  return target.style.transform = "translateY(" + (-move) + "px)";
};

trap = function(target, trap, bottom, top, bxtra, txtra) {
  var bottomStretch, topStretch;
  if (bottom == null) {
    bottom = true;
  }
  if (top == null) {
    top = true;
  }
  if (bxtra == null) {
    bxtra = 0;
  }
  if (txtra == null) {
    txtra = 0;
  }
  if (bottom) {
    bottomStretch = (trap.offsetTop + trap.offsetHeight) - (window.scrollY + window.innerHeight) + bxtra;
  } else {
    bottomStretch = 0;
  }
  if (top) {
    topStretch = (trap.offsetTop + target.offsetHeight) - (window.scrollY + window.innerHeight) + txtra;
  } else {
    topStretch = 0;
  }
  if (topStretch > 0) {
    return target.style.transform = "translateY(" + topStretch + "px)";
  } else if (bottomStretch < 0) {
    return target.style.transform = "translateY(" + bottomStretch + "px)";
  } else {
    return target.style.transform = "translateY(0px)";
  }
};
